  @apply shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight my-3;
}
.frm-input:focus {
  @apply outline-none shadow-outline;
}
*/
.frm-label {
  @apply block text-gray-700 text-sm font-bold mb-2;
}
.form-group {
  @apply my-8;
}

.carousel-smaller,
.carousel > img {
  max-width: 90%;
  max-height: 200px;
}

@screen sm {
  .carousel-smaller,
  .carousel > img {
    max-width: 45%;
    max-height: 400px;
  }
}

#modal {
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal:empty {
  display: none;
}

#modal > div {
  background-color: white;
  max-width: 500px;
  padding: 15px;
  border-radius: 5px;
  text-align: center;
}

#modal .btn {
  display: inline-block;
  margin-right: 1rem;
  margin-top: 2rem;
}

@tailwind utilities;
